body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  box-sizing: border-box;
}

nav {
  display: flex;
  justify-content: space-between;
  background: #1d809f;
  align-items: center;
  padding-right: 20px;
  padding-left: 20px;
}
.logo {
  font-size: 30px;
  color: blue;
  color: #fff;
}

nav ul {
  list-style: none;
}
nav ul li {
  display: inline-block;
  margin-right: 8px;
}
nav ul li a {
  text-decoration: none;
  color: #fff;
}

header {
  margin: 0 auto;
  text-align: center;
  padding-top: 300px;
}

header h1 {
  margin: 0;
  padding: 0;
  font-size: 4rem;
  color: rgb(33, 37, 41);
}

header p {
  font-weight: 700;
  font-size: 1.75rem;
  color: rgb(33, 37, 41);
  margin-bottom: 30px;
}

header a {
  box-shadow: 0 3px 3px 0 rgba(0, 0, 0, 0.1);
  font-weight: 700;
  background-color: #497a81 !important;
  border-color: #1d808f !important;
  color: #fff !important;
  padding: 1.25rem 2.5rem;
  text-decoration: none;
  border-radius: 0.25rem;
  display: inline-block;
}

.services {
  background-color: #1d809f;
  padding-top: 120px;
  padding-bottom: 120px;
  text-align: center;
}

.services h3 {
  font-size: 16px;
  text-transform: uppercase;
  color: rgb(236, 184, 7);
  margin: 0;
  padding: 0;
}

.services h2 {
  font-size: 48px;
  color: #fff;
  margin-bottom: 48px;
  margin-top: 0;
}

.services > .row {
  display: flex;
  justify-content: space-around;
}
.services > .row span {
  background-color: #fff;
  width: 112px;
  height: 112px;
  display: inline-block;
  border-radius: 50%;
  color: #1d809f;
  font-size: 36px;
  line-height: 120px;
}
.services > .row .fab {
  contain: none;
}
.services > .row h4 {
  margin-bottom: 0.5rem;
  font-family: inherit;
  font-weight: 500;
  line-height: 1.2;
  color: inherit;
  font-size: 24px;
  color: #fff;
}
.services > .row p {
  font-size: 16px;
  color: #fff;
}

